import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { PluginRoles } from '@ali/4ever-bamboo';
import { IconButton as Button } from '@ali/we-toolbar';
import { ToolbarLocalfileNormal } from '@ali/we-icon';
import { selectFile } from '@ali/4ever-factory';
import { ToolbarMode } from '@ali/4ever-component';

export interface EmbedMenuProps {
  controller: Controller;
  locale: any;
  toolbarMode?: ToolbarMode;
}

const iconStyle: React.CSSProperties = {
  width: 20,
  height: 20,
};

export default function EmbedButton(props: EmbedMenuProps) {
  const { controller, locale, ...rest } = props;
  const { value } = controller;
  const isDisabled = controller.query('isSelectionInImage') || controller.query('isSelectionInLinkCard');
  const handleClick = isDisabled
    ? () => { }
    : () => controller.run('onAction', selectFile());

  return (
    <Button
      {...rest}
      title={locale.buttonTitle}
      tooltip={locale.buttonTitle}
      className="bi-toolbar-embed"
      testid="bi-toolbar-embed"
      icon={<ToolbarLocalfileNormal style={iconStyle} />}
      disabled={isDisabled}
      onClick={handleClick}
      role={PluginRoles.embed}
    />
  );
}
